body,
div,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
p {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
button {
  border: 0;
  word-break: keep-all;
  white-space: nowrap;
  cursor: pointer;
}
input:focus {
  outline: none;
}
a {
  color: inherit;
  text-decoration: none;
}

body {
  background-color: #181818;
  color: #fff;
}
